'use client';

import { ReactNode, useState } from 'react';
import { Menu, X, Bell, LogOut, Settings, User } from 'lucide-react';

interface LayoutProps {
  children: ReactNode;
}

export const MainLayout = ({ children }: LayoutProps) => {
  const [mobileOpen, setMobileOpen] = useState(false);

  const menuItems = [
    { href: '/', label: 'Tableau de bord', icon: 'üìä' },
    { href: '/equipment', label: '√âquipements', icon: 'üíª' },
    { href: '/users', label: 'Utilisateurs', icon: 'üë•' },
    { href: '/ip-addresses', label: 'Adresses IP', icon: 'üåê' },
    { href: '/servers', label: 'Serveurs', icon: '‚öôÔ∏è' },
    { href: '/tickets', label: 'Tickets Helpdesk', icon: 'üé´' },
  ];

  const NavLinks = () => (
    <nav className="flex flex-col md:flex-row md:items-center gap-2 md:gap-1">
      {menuItems.map((item) => (
        <a
          key={item.href}
          href={item.href}
          className="flex items-center gap-2 px-3 py-2 rounded-xl text-sm font-medium text-slate-700 hover:text-slate-900 hover:bg-slate-100 transition-colors"
        >
          <span className="text-lg">{item.icon}</span>
          <span>{item.label}</span>
        </a>
      ))}
    </nav>
  );

  return (
    <div className="min-h-screen bg-white text-slate-900">
      {/* Top Navigation */}
      <header className="sticky top-0 z-30 backdrop-blur-xl bg-white/80 border-b border-slate-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between gap-4 text-slate-900">
          <div className="flex items-center gap-3">
            <button
              onClick={() => setMobileOpen((v) => !v)}
              className="md:hidden p-2 rounded-lg bg-slate-100 hover:bg-slate-200 border border-slate-200 transition-colors"
              aria-label="Ouvrir le menu"
            >
              {mobileOpen ? <X size={20} /> : <Menu size={20} />}
            </button>

            <div className="flex items-center gap-3">
              <div className="h-10 w-10 rounded-2xl bg-gradient-to-br from-blue-500 to-cyan-400 flex items-center justify-center font-bold text-white shadow-lg shadow-blue-500/30">
                MM
              </div>
              <div>
                <p className="text-sm text-slate-600">MasterMonitor</p>
                <p className="text-lg font-semibold text-slate-900">Dashboard</p>
              </div>
            </div>
          </div>

          <div className="hidden md:flex items-center gap-3">
            <NavLinks />
          </div>

          <div className="flex items-center gap-3">
            <div className="hidden sm:flex items-center bg-slate-100 border border-slate-200 rounded-xl px-3 py-2">
              <input
                type="text"
                placeholder="Rechercher..."
                className="bg-transparent text-sm text-slate-800 placeholder:text-slate-400 focus:outline-none w-40"
              />
            </div>
            <button className="relative p-2 rounded-lg bg-slate-100 hover:bg-slate-200 border border-slate-200 transition-colors">
              <Bell size={18} />
              <span className="absolute -top-1 -right-1 h-2.5 w-2.5 rounded-full bg-red-500 animate-pulse" />
            </button>
            <button className="p-2 rounded-lg bg-slate-100 hover:bg-slate-200 border border-slate-200 transition-colors">
              <Settings size={18} />
            </button>
            <button className="hidden sm:flex items-center gap-2 px-3 py-2 rounded-xl bg-slate-100 hover:bg-slate-200 border border-slate-200 transition-colors">
              <User size={18} />
              <span className="text-sm font-medium text-slate-900">Admin</span>
            </button>
            <button className="p-2 rounded-lg bg-red-500/90 hover:bg-red-500 text-white transition-colors">
              <LogOut size={18} />
            </button>
          </div>
        </div>

        {/* Mobile drawer */}
        {mobileOpen && (
          <div className="md:hidden border-t border-slate-200 bg-white px-4 pb-4 space-y-2 text-slate-900">
            <NavLinks />
          </div>
        )}
      </header>

      {/* Main content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6 text-slate-900">
        {children}
      </main>
    </div>
  );
};

export default MainLayout;
